#modal-backdrop{z-index:1000;position:fixed;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0);-webkit-transition:background-color 1s;transition:background-color 1s;display:none
}
#modal-backdrop.active{display:block
}
#modal-backdrop.open{background-color:rgba(0,0,0,0.3)
}
#modal-backdrop .modal-window{position:relative;background-color:#fff;width:80vw;margin:80px auto;padding:20px;min-height:calc(100vh - 240px);max-height:calc(100vh - 160px);overflow-y:auto
}
#modal-backdrop .closer{color:#999;font-size:36px;position:absolute;right:16px;top:10px;line-height:1;padding:4px;border:none
}
#modal-backdrop .closer:focus{outline:none
}

#dialog-backdrop{z-index:1000;position:fixed;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0);-webkit-transition:background-color 1s;transition:background-color 1s;display:none
}
#dialog-backdrop.active{display:block
}
#dialog-backdrop.open{background-color:rgba(0,0,0,0.3)
}
#dialog-backdrop .dialog-window{position:relative;background-color:#fff;width:60vw;min-width:300px;max-width:600px;margin:80px auto;padding:40px 50px
}
#dialog-backdrop .message{font-size:20px;color:#666
}
#dialog-backdrop .buttons{text-align:center;margin-top:20px
}
#dialog-backdrop .buttons button{background-color:#bbb;border:none;margin-right:20px;text-transform:uppercase;font-size:20px;font-weight:500;color:#000;padding:8px 16px
}
#dialog-backdrop .buttons button:last-child{margin-right:0
}
#dialog-backdrop .buttons button.btn-warning{background-color:#999
}
#dialog-backdrop .closer{color:#999;font-size:36px;position:absolute;right:16px;top:10px;line-height:1;padding:4px;border:none
}
#dialog-backdrop .closer:focus{outline:none
}

